export async function shareMemory(
  bucket,
  authURL,
  memoryID,
  emailList,
  memoryShareCallback,
) {
  try {
    /* eslint-disable no-console */
    console.log('share memory', bucket, authURL, memoryID, emailList);
    const shareResults = await api.shareMemory(
      bucket,
      authURL,
      memoryID,
      emailList,
    );
    memoryShareCallback(shareResults);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

ShareComponent.propTypes = {
  confirm: PropTypes.bool.isRequired,
  callback: PropTypes.func.isRequired,
  closeCallback: PropTypes.func.isRequired,
  memoryID: PropTypes.string.isRequired,
};
